export default {
  /*
  初始化数据
  */
  data() {
    return {
      address: "",
      myLocation: null,
      sexList: ["先生", "女士"],
      sex: "",
      tagList: ["家", "公司", "学校"],
      tag: ""
    };
  },
  computed: {
    // 计算属性的 getter
  },
  onLoad: function() {},
  onShow: function() {
    console.log("pages/user/addAddress");
  },
  methods: {
    _chooseLocation() {
      var _this = this;
      uni.chooseLocation({
        success: function(res) {
          _this.address = res.name;
          _this.myLocation = res;
        }
      });
    },
    _chooseSex(value) {
      this.sex = value;
    },
    _chooseTag(value) {
      this.tag = value;
    },
    formSubmit: function(e) {
      var data = e.detail.value;
      //参数校验
      data.sex = this.sex;
      data.tag = this.tag;
      console.log(data);
    }
  }
};
